@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap');

* {
    padding           : 0;
    margin            : 0;
    border            : 0;
    -webkit-box-sizing: border-box;
    box-sizing        : border-box
}

*::before,
*:after {
    -webkit-box-sizing: border-box;
    box-sizing        : border-box
}

:focus,
:active {
    outline: none
}

a:focus,
a:active {
    outline: none
}

nav,
footer,
header,
aside {
    display: block
}

html,
body {
    height                  : 100%;
    width                   : 100%;
    font-size               : 100%;
    line-height             : 1;
    font-size               : 14px;
    -ms-text-size-adjust    : 100%;
    -moz-text-size-adjust   : 100%;
    -webkit-text-size-adjust: 100%
}

input,
button,
textarea {
    font-family: inherit
}

input::-ms-clear {
    display: none
}

button {
    cursor: pointer
}

button ::-moz-focus-inner {
    padding: 0;
    border : 0
}

a {
    text-decoration: none
}

a:visited,
a:hover {
    text-decoration: none
}

ul li {
    list-style: none
}

img {
    vertical-align: top
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size  : inherit;
    font-weight: 400
}

body {
    background-color: rgb(0, 0, 0);
    color           : #ffff;
    font-family     : 'Nunito', sans-serif;
    font-weight     : 400;
}

.app {
    display   : flex;
    overflow  : hidden;
    min-height: 100%;
}